    //Cria documento e retorna sua chave
    const createDocumentKey = async (keySign: string) => {
        const fetchDocumentKey = fetch('https://nestrental.com.br:80/api/clicksign/create-document', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            }, body: JSON.stringify({
                "list": {
                    "document_key": window.localStorage.getItem('key_document'),
                    "signer_key": keySign,
                    "sign_as": "sign",
                    "refusable": true,
                    "message": `Prezado ${nomeFantasiaNewCLient? nomeFantasiaNewCLient : clientData.entidade.nome_fantasia},
                    \nPor favor assine o documento.\n\nQualquer dúvida estou à disposição.\n\nAtenciosamente,\nNest Rental.`
                }
            })
        })
        const response = await fetchDocumentKey;
        const json = await response.json();
        const key = await json?.key_document_signer;
        setKeyDocumentSign(key);
        await sendNotificationDocument(key);
        //window.localStorage.setItem('key_document', '');
    }

